<template>
    
    <div > 
    
        <v-img id='top-left' :src='imgg' cover
            src='iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVR42mM8f/5KPQAH2gLzOV3TUgAAAABJRU5ErkJggg=='
            aspect-ratio='0.577' :width='$vuetify.breakpoint.mdAndUp? "25vw" : "45vw"'  style='z-index: 0;'>
        </v-img>
        
    <v-layout row fill-height fluid style='min-height:89.3vh' py-3 :wrap='$vuetify.breakpoint.mdAndDown'
        justify-center
     >
        <v-flex xs10  sm6 md3 shrink offset-lg1 pt-5 mt-5  >

            
            <v-img 
                class='mt-5' 
                :lazy-src="mainImage"
                :src="mainImage " 
            
                contain aspect-ratio=1 max-width='90vw; height: auto' 
                :style='dStyle' 
            > </v-img>
            </intersect>

            
        </v-flex>
        <v-flex
            xs12 
            offset-xs0
            :pa-2='$vuetify.breakpoint.smAndDown'

            lg10        

            offset-lg1 
            md10
            
            mb-5
            pb-5
            class=''>
        
            <v-layout style='width: 100%'>

                <v-flex>
                    <h1 class='headline' style='opacity: .5'> {{data.sub_name}} <span class='subheading'> {{data.datalist.length}} </span> </h1> 
                </v-flex>
                <v-flex  shrink align-self-end pr-3>
                   <!--  <img :src="require('@/assets/arya.png')" width=200 > </img> -->
                </v-flex>
            </v-layout>
            
           
            

            <v-layout  row wrap justify-center align-space-between fill-height style='will-change: transform,opacity; '>
                    <v-flex 
                    
                        class='product' 
                        style='padding: 0px 5px 0px 5px;margin-top: 0px; max-height: 10vmax !important'  
                        
                        py-0
                        xs12 sm6 lg4 xl4 v-for='(item,k) in data.datalist' 
                        v-observe-visibility="{
                            callback: (isVisibile,entry) =>  {
                
                            pState('p_'+k,isVisibile,entry)
                            },
                            intersection: {
                                threshold: .2
                            },
                            throttle: 50,
                            once: false
                            
                        }"
                    >
                        <router-link append :to='"product/"+String(item.id)' tag='div' style='cursor:pointer'>
                            <v-layout row class='p-in' wrap  align-center fill-height  :style='dynamic' style='padding-top: 4px !important; padding-bottom: 4px !important'>
                                <v-flex xs2 class='p-img '  pl-2>
                                
                                    <!-- :src="item.img_product && item.img_product.length > 1 ? item.img_product : item.img_blueprint"  -->
                                    <v-img contain class='pItem-img ' style=''  
                                        :lazy-src="'/loading.svg' " 
                                        :src="(item.img_product.length > 1 && item.img_product ? item.img_product  : item.img_blueprint  )" 
                                    
                                    aspect-ratio=0.625> </v-img>
                                </v-flex>
                                <v-flex xs10 pl-3 pr-1 mt-0 class='p-content' style='opacity: 1;'>
                                    <h1  class='display-4 mb-1' style='color: #008364; opacity: 1; font-size: 16px !important;font-weight: 450 !important'> {{extractName(item.name).mainName}}</h1>
                                    <div class='subhheading' style='opacity: .6; font-size: 1rem !important; line-height: 120%' v-for='i in extractName(item.name).subName'> {{  i.split(' ').map( (k,ii) => {
                                            
                                            if(k)
                                            {
                                                //var temp = k.toUpperCase()
                                                var temp = k

                                                if(ii == 0)
                                                {
                                                    return temp.match(/ISO/gi) ? temp.toUpperCase() : temp.toLowerCase()

                                                }


                                                var temp2 = temp.charAt(0).toUpperCase()
                                                return temp
                                            }
                                            else
                                            return k
                                        } ).join(' ')  }} </div>
                                    <p class='body-2 mt-0' style='margin-bottom: 0px !important; opacity: .7'> {{item.design_code}} </p>
                                </v-flex>
                            </v-layout>
                        
                        </router-link>
                    </v-flex>

            </v-layout>
        </v-flex> 
    </v-layout>

    </div>
</template>

<script>
export default {
    
    name: 'products',
    props: {
        data: {
            type: Object,
            required: true
        },
        mainImage: {
            type: String,
            required: true
        }
    },

    components: {
        Intersect : ()=> import('vue-intersect')
    },

    data(){

        return {

            
            imgg: require('@/assets/top-left.svg') ,
            
            category: 'p11.png',
            name: 'COMPRESSION TUBE FITTINGS',

            dynamic: {
                height: 'auto'
            },

            aState: 0,
            animObj: {

            },

            dStyle: {
                transform: this.$vuetify.lgAndUp ? 'translate(0%,50%)' : false,
                margisnLeft: '-15%'
                
            },
            visibleAmount : 0
        }
    },

    computed:{
        
    },

    methods: {

        extractName(n){
            let obj =  {
                
            }

            n = n.split('|')

            obj.mainName = n[0]
            obj.subName = n[1] ? n[1].split(',') : n[1] 
            //console.log(obj.subName)

            return obj
        },
        pState(id,v,e)
        {
            let d = id.split('_')[1]
            if(!this.animObj[d])
            {
                 this.animObj[d] = {
                    previousY : 0,
                    previousRatio : 0,
                    visible: false,
                    running: false,
                    el: e.target
                }
                //console.log('initi')
            }
               
            let yVal = 0


            if(!e.isIntersecting)
            {
                this.animObj[d].visible = false
                this.animObj[d].el.style.opacity = 0
            }
            
            this.animObj[d].currentY= e.boundingClientRect.y

            if(this.animObj[d].currentY < this.animObj[d].previousY && this.animObj[d].currentY != this.animObj[d].previousY)
                this.animObj[d].direction = 0
            else
                this.animObj[d].direction = 1


            if(!this.animObj[d].visible && !this.animObj[d].running && v)
            {
                this.animObj[d].running = true

                let yVal
                if(this.animObj[d].direction == 0)
                    yVal = 50
                else
                    yVal = -50
                    

                    let a = this.$anime({
                    targets: /*'.'+id, */ this.animObj[d].el,
                    opacity: [0,1],
                    translateZ: [0,1],
                    translateY: [yVal,0],
                    
                    //translateZ: [0,0],
                    
                    /*delay: (el,i) => {
                        return (d%5)*200 + (++i*100)
                    }, */

                    delay: (el,i) => {
                        return /*(d%5)*100+(i*0) */ (d%3)*100
                    },
                    duration: 300,
                    easing: 'linear'

                }).finished.then( r => {
                    //console.log('pre')
                    
                        this.animObj[d].running = false
                        this.animObj[d].visible = true

                       // console.log('finished')

                        
                }  )
            }
            this.animObj[d].previousY = this.animObj[d].currentY
            
                
        },

        setId(id){
            id = ++id
            //console.log(`id is ${id}`)

            if(id%3 ==0)
                this.aState++

            return "p_"+this.aState;
        } 
    },


    mounted(){

        this.data.sub_name ? true : this.$router.push('/category')

        var pList = document.getElementsByClassName('product')
        var large = 0

        //console.log(pList.length)
        

    

        for( var i of pList )
        {
            //console.log(i)
            let r = i.getBoundingClientRect()
            if(r.height > large)
                large = r.height
        }
            
        

        this.dynamic.height = large-7 + 'px'
        //console.log(this.dynamic)
        
    }

}
</script>

<style>

.wrap{
    word-wrap: break-word;
    white-space: normal;
}

.pItem-img{
  
}

.pItem-box{
    background:  #ebecec;
    border-radius: 100%;
}

.p-img{
    border-radius: 50%;
    border: 0px solid #BDBDBD;
    border-right: 0px;
    border-top: 0px;
    border-bottom: 0px;
}



.p-img > div {
 
}

.p-content{
    
}

.product {
    transition: all .15s linear;
}


.product:hover{
    transform: scale(1.1) rotate(-2deg);
    

}


.product:hover .p-in{
    background: #a0efdc;
    border-top-left-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 2rem;
    border-top-right-radius: 2rem;
    
}



.p-in{
    border: 1px solid #BDBDBD;
    border-top-left-radius: 0rem;
    border-bottom-right-radius: 0rem;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
    transition: all .2s linear;
}

.v-image__image--pre {
    filter: blur(0px) !!important;
    -webkit.filter: blur(0px) !important;
}


</style>
